.titreArticle{
      background-color: rgba(0,0,0,.5);
      text-align: center;
      margin: 20px 0;
      color:white;
      font-size: 30px;
      padding: 20px 0;
      border-radius: 10px;
}

.parcours{
      h1{
            background-color: rgba(0,0,0,.5);
            text-align: center;
            margin: 20px 0;
            color:white;
            font-size: 30px;
            padding: 20px 0;
            border-radius: 10px;
      }
}

.article{
      p{
            img {
                  height: 215px ;
                  border-radius: 10px;
            }
      }
      padding: 50px 0;
      display:flex;
      flex-direction: row;
      justify-content: space-between;

      .image{
            width:50%;
            padding-right : 20px;
      }
      .content{
            width:50%;
            text-align:center;
            color:white;
            background-color: rgba(0,0,0,.4);
            border-radius: 10px;
            margin-left:20px;
            h2{
                  font-size: 20px !important;
                  color:white;
                  padding: 20px;
            }
            p{
                  font-size: 15px !important;
                  color:white;
                  padding: 10px;
            }
      }
}